.App {
  text-align: center;
  background-color: #f7f2f2
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
  text-decoration: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navBar {
  list-style-type: none;
  height: 100px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #f1f1f1;
}

.text-link {
  display: block;
  color: white;
  font-size: 30;
  text-align: center;
  position: relative;
  padding: 40px;
  text-decoration: none;
  float: left;
  color: black;
}

.image-navbar-link {
  float: right;
  height: 100px;
  width: 100px;
}

.text-link:hover {
  background-color: #555;
  color: white;
}

#portfolioElement {
  padding-left: 25%;
  padding-right: 25%;
}

