#portfolioPartyToolsApp {
  margin-top: 1.25%;
  margin-left: 5%;
  margin-right: 5%;
  border-style: groove;
  border-radius: 10px;
  height: 450px;
}

#portfolioPartyToolsApp p, h2, h3, a{
  padding-left: 2.5%;
  padding-right: 2.5%;
}

#portfolioWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

#interestsWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

#interestsDiv {
  margin-top: 1.25%;
  margin-left: 5%;
  margin-right: 5%;
  border-style: groove;
  border-radius: 10px;
  height: 400px;
}

#interestsDiv p, h2, h3, a{
  padding-left: 2.5%;
  padding-right: 2.5%;
}

#interestsDiv img {
  margin-left: 5px;
  border-radius: 20px;
  height: 225px;
  width: 300px;
}

#pitchAndPuttImageWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 10px;
}

#libraryWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding-left: 15%;
  padding-top: 5%;
}
